@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,300;1,400&display=swap);
*{margin:0;padding:0}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#09053a;background-attachment:fixed;color:#ddecff;font-size:18px;font-family:"Source Sans Pro", sans-serif;font-family:"Fira Code", code;scroll-behavior:smooth}body a:visited,body a{color:#ddecff;text-decoration:none}#root{min-height:100vh;position:relative}.sections{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;overflow:scroll}section{width:75%;padding:6rem 0rem}section h1,section h2,section h3{font-family:"Source Sans Pro", sans-serif;color:#3cffb9}section h1{font-size:4rem;margin:1rem 0}section h2{font-size:2.5rem;font-weight:600;margin-bottom:3rem;text-align:center}section h3{font-size:1.5rem;font-weight:600;margin-bottom:2rem}

#Landing{min-height:calc(100vh - 16rem);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative}#Landing h1,#Landing h2{font-family:"Source Sans Pro", sans-serif}#Landing h1{margin:1rem 0}#Landing h1 span{color:#d6deff}#Landing h2{color:#ddecff;text-align:left;font-size:2rem}#Landing #cta{margin-top:3rem;justify-self:flex-end}#Landing .link-button{color:#3cffb9;display:inline-block;padding:0.6rem 1.4rem;margin-right:1rem;border:2px #3cffb9 solid;border-radius:0.3rem;transition:all 0.2s ease-in-out}#Landing .link-button:hover{color:#00ffc2;background-color:rgba(0,255,162,0.233)}#Landing #chevron{position:absolute;bottom:2rem;right:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;font-size:2.5rem;color:#3cffb9}#Landing #chevron svg{-webkit-animation:chevron 3s infinite ease-in alternate;animation:chevron 3s infinite ease-in alternate;margin-top:0;border-radius:1rem}#Landing #chevron svg:last-child{font-size:3rem;-webkit-animation-delay:-0.3s;animation-delay:-0.3s;margin-top:-1.8rem}@-webkit-keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}

nav{position:sticky;position:-webkit-sticky;padding:0.75rem 2rem;top:0;font-size:1.2rem;color:#3cffb9;z-index:99;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;background:rgba(255,255,255,0.2);backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(15px);border:1px solid rgba(255,255,255,0.18)}nav span,nav a:visited,nav a{padding:0.3rem 0.6rem;color:#ddecff}nav a:visited,nav a{text-decoration:none;transition:all 0.2s ease-in-out}nav a:visited:hover,nav a:hover{color:#3cffb9}

#About{max-width:70ch}#About #skills{margin-top:5rem;display:-webkit-flex;display:flex}#About #skills div{margin-right:8rem}

.Project{margin:auto;display:grid;grid-template-columns:45% 55%;margin-bottom:5rem;margin-top:5rem}.Project .project-preview{border-radius:0.75rem;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}.Project .project-preview .image{display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:column;flex-direction:column;position:relative;border-radius:0.75rem;box-shadow:0.15rem 0.15rem 0 2px #00ffc2;transition:all 0.3s ease-in-out}.Project .project-preview .image img{-webkit-transform:translate(-0.5rem, -0.5rem);transform:translate(-0.5rem, -0.5rem);transition:all 0.3s ease-in-out;border-radius:0.75rem;max-width:100%;-webkit-filter:brightness(0.75);filter:brightness(0.75)}.Project:hover .project-preview .image{box-shadow:0.1rem 0.1rem 0 1px #3cffb9}.Project:hover .project-preview .image img{-webkit-filter:brightness(1);filter:brightness(1);-webkit-transform:translate(0, 0);transform:translate(0, 0)}.Project .project-info{margin:auto;margin-left:0;margin-right:0;height:12rem;max-width:60ch;border-radius:0.75rem;border-top-left-radius:0;border-bottom-left-radius:0;padding:0.75rem 1rem;padding-left:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:rgba(34,34,105,0.82);-webkit-justify-content:space-evenly;justify-content:space-evenly}.Project .project-info .title{text-align:center;font-size:1.4rem;margin-bottom:1rem}.Project .project-info .title span{color:#3cffb9}.Project .project-info .description{position:relative;padding-right:0.5rem;font-size:1.1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}.Project .project-info .description .project-meta{font-size:1rem;margin-top:1rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.Project .project-info .description .project-meta span{margin-right:1rem;color:#3cffb9}.Project .project-info .description .project-meta span.--link{color:#ddecff;margin:0}.Project .project-info .description .project-meta span.--link i{position:relative;margin-right:3ch}.Project .project-info .description .project-meta span.--link i svg{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.Project .project-info .description .project-meta span.--link:hover{color:#3cffb9}#Projects :nth-child(odd) .Project{grid-template-columns:none;grid-template-columns:55% 45%}#Projects :nth-child(odd) .Project .project-preview{-webkit-order:2;order:2;-webkit-justify-content:flex-start;justify-content:flex-start}#Projects :nth-child(odd) .Project .project-info{-webkit-order:1;order:1;margin-left:auto;padding:0.75rem 1rem;padding-left:2rem;border-radius:0.75rem;border-top-right-radius:0;border-bottom-right-radius:0}

#sidelinks{position:fixed;right:0;top:calc(50vh + 2rem);margin-right:1.5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}#sidelinks .container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#sidelinks .container a:visited,#sidelinks .container a{transition:all 0.2s ease-in-out;color:#ddecff;text-decoration:none;padding:0.5rem 0;font-size:1.5rem;position:relative}#sidelinks .container a:visited span,#sidelinks .container a span{transition:all 0.2s ease-in-out;width:8ch;text-align:center;border-radius:0.25rem;color:#3cffb9;font-size:1rem;padding:0.5rem;display:none;position:absolute;right:3rem;top:50%;-webkit-transform:translateY(-55%);transform:translateY(-55%);background:#030e38;box-shadow:1px 1px 2px #3cffb9}#sidelinks .container a:visited:hover,#sidelinks .container a:hover{color:#3cffb9}#sidelinks .container a:visited:hover span,#sidelinks .container a:hover span{opacity:1;display:inline}#sidelinks .container::before,#sidelinks .container::after{content:"";width:0;border:solid #ddecff 1.5px;background-color:#ddecff;position:fixed;height:calc((80vh / 2) - 4rem);border-radius:2rem;right:50%}#sidelinks .container::before{top:0;-webkit-transform:translate(50%, -105%);transform:translate(50%, -105%)}#sidelinks .container::after{-webkit-transform:translate(50%, 105%);transform:translate(50%, 105%);bottom:0}

